:root {
  --color-bg-white: #ffffff;
  --color-bg-grey: #F6F6F6;
  --color-dark-grey:  #828282;
  --color-main-black: #000000;
  --font-family-main: 'Fjalla One', 'Abril Fatface' ,sans-serif;
  --font-family-little: Roboto, 'Fjalla One'; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  background-color: var(--color-bg-white);
  font-family: var(--font-family-main); }

.header__title {
  font-size: 47px;
  line-height: 58px;
  width: 124px;
  height: 106px; }

.header__photo-demonstration {
  margin-bottom: 83px; }

.header__about-me {
  margin: 0;
  margin-bottom: 36px;
  font-family: var(--font-family-little);
  width: 205px;
  height: 44px; }

nav {
  position: relative; }

.nav__button {
  position: absolute;
  top: -3px;
  right: 0px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none; }

.nav__closed .nav__button::before {
  content: "";
  position: absolute;
  top: 15px;
  right: 14px;
  width: 24px;
  height: 2px;
  background-color: var(--color-main-black);
  box-shadow: 0 8px 0 0 var(--color-main-black), 0 16px 0 0 var(--color-main-black); }

.nav__opened .nav__button {
  top: -3px;
  right: -0px; }
  .nav__opened .nav__button::before, .nav__opened .nav__button::after {
    content: "";
    position: absolute;
    top: 26px;
    right: 13px;
    width: 24px;
    height: 2px;
    background-color: var(--color-main-black); }
  .nav__opened .nav__button::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .nav__opened .nav__button::after {
    transform: rotate(-45deg); }

.nav__list {
  list-style: none;
  font-size: 25px;
  line-height: 42px;
  color: var(--color-dark-grey); }

.nav__opened .nav__list {
  display: block; }

.nav__closed .nav__list {
  display: none; }

.nav__list-element {
  cursor: pointer; }
  .nav__list-element:link, .nav__list-element:visited, .nav__list-element:hover, .nav__list-element:active {
    color: var(--color-main-black); }

.nav__language {
  text-decoration: none;
  color: var(--color-main-black); }

.about-me {
  padding-top: 81px;
  padding-bottom: 29px;
  background-color: var(--color-bg-grey);
  color: var(--color-main-black);
  font-family: var(--font-family-little);
  font-size: 18px;
  line-height: 22px;
  text-align: center; }

.about-me .about-me__title {
  margin-bottom: 29px;
  font-size: 34px;
  line-height: 42px;
  text-align: center; }

.about-me__describe-first,
.about-me__describe-second {
  padding-bottom: 29px; }

.skills {
  padding-top: 83px;
  background-color: var(--color-bg-white);
  text-align: center; }

.skills__title {
  color: var(--color-main-black);
  font-family: var(--font-family-main);
  font-size: 34px;
  line-height: 42px;
  text-align: center;
  margin-bottom: 50px; }

.skills__advantages {
  font-size: 18px;
  line-height: 22px;
  font-family: var(--font-family-little); }

.skills__list {
  padding: 0;
  margin: 0;
  list-style: none; }

.skills__img {
  width: 80px;
  height: 80px; }

.skills__img-sql {
  width: 140px; }

.portfolio {
  padding-top: 81px;
  text-align: center;
  background-color: var(--color-bg-grey); }

.portfolio__title {
  font-size: 34px;
  line-height: 42px;
  color: var(--color-main-black);
  margin-bottom: 65px;
  font-family: var(--font-family-main); }

.portfolio__list {
  padding: 0;
  margin: 0;
  list-style: none; }

.portfolio__img {
  width: 300px; }

.contacts {
  text-align: center;
  padding-top: 82px;
  margin-top: -16px; }

.contacts__title {
  font-size: 34px;
  line-height: 42px;
  font-family: var(--font-family-main);
  margin-bottom: 27px; }

.contacts__welcome {
  font-size: 18px;
  line-height: 22px;
  width: 255px;
  height: 44px;
  margin: auto;
  margin-bottom: 39px; }

.contacts__button {
  width: 316px;
  height: 42px;
  background-color: var(--color-main-black);
  color: var(--color-bg-white);
  border-radius: 50px;
  font-size: 18px;
  line-height: 22px;
  font-family: var(--font-family-little);
  margin-bottom: 50px; }

.contacts__list-social {
  list-style: none;
  margin-bottom: 50px; }

.contacts__describe-link {
  font-size: 18px;
  line-height: 22px;
  color: var(--color-dark-grey); }

.contacts__describe-link-soc {
  margin: 0; }

.contacts__img {
  width: 40px;
  height: 40px;
  cursor: pointer; }
