nav {
    position: relative;
}

.nav__button {
    position: absolute;
    top: -3px;
    right: 0px;
    z-index: 2;
    display: block;
    width: 50px;
    height: 50px;
    background-color: transparent;
    cursor: pointer;
    border: none;
}

.nav__closed .nav__button::before {
    content: "";
    position: absolute;
    top: 15px;
    right: 14px;
    width: 24px;
    height: 2px;
    background-color: var(--color-main-black);
    box-shadow:
      0 8px 0 0 var(--color-main-black),
      0 16px 0 0 var(--color-main-black);
 }

 .nav__opened .nav__button {
    top: -3px;
    right: -0px;
  
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 26px;
      right: 13px;
      width: 24px;
      height: 2px;
      background-color: var(--color-main-black);
    }
  
    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }
  
    &::after {
      transform: rotate(-45deg);
    }
  }

.nav__list {
    list-style: none;
    font-size: 25px;
    line-height: 42px;
    color: var(--color-dark-grey);
}

.nav__opened .nav__list {
    display: block;
}

.nav__closed .nav__list {
    display: none;
}

.nav__list-element {
    cursor: pointer;
    &:link,
    &:visited,
    &:hover,
    &:active {
        color: var(--color-main-black);
    }
}


.nav__language {
    text-decoration: none;
    color: var(--color-main-black);
}
